body {
	background-color: var(--background);
}

.bar-container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 90vh;
}

.inner-bar-container {
	height: 100%;
	width: 95%;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

nav {
	display: flex;
	height: 10vh;
	width: 100vw;
	padding: none;
	margin: none;
	justify-content: center;
	align-items: center;
	background-color: var(--higher-background);
	overflow: hidden;
}

nav * {
	height: 30px;
	margin: 0 1.5em;
	color: var(--text);
	user-select: none;
}

button,
select {
	background-color: var(--accent);
	overflow: hidden;
}

select {
	border: none;
	outline: none;
	box-shadow: none;
}

.bar {
	display: flex;
	/*margin: 5px;*/
	color: var(--text);
	justify-content: center;
	align-items: center;
	border-radius: 20px;
	user-select: none;
	overflow: hidden;
}

.bar:hover {
	filter: brightness(80%);
}

option {
	background-color: var(--higher-background);
	border: none;
	outline: none;
	box-shadow: none;
}

.algorithmsTitle {
	background-color: var(--background);
	color: var(--text);
	font-size: 1.05em;
	overflow: hidden;
}

.algorithmsTitle:disabled {
	color: var(--text);
	opacity: 100%;
}

.button,
.button-disabled,
select {
	box-sizing: border-box;
	width: 10%;
	height: 50%;
	border-radius: 10px;
	place-items: center;
	color: var(--text);
	font-weight: 800;
	overflow: hidden;
}

select {
	background-color: var(--accent);
	padding: 0;
	font-weight: bold;
	border: none;
	box-shadow: none;
	text-align-last: center;
	outline: none;
	overflow: hidden;
}

.button,
.button-disabled {
	border: none;
	box-shadow: none;
	display: flex;
	box-sizing: border-box;
	align-content: center;
	justify-content: center;
	padding: 0.5% 0;
	background-color: var(--accent);
	font-weight: bold;
	overflow: hidden;
}

.button * {
	margin: 0;
}

option {
	border: none;
	box-shadow: none;
	outline: none;
}

select:disabled,
button:disabled,
.sliderBox input:disabled,
.disabled {
	color: var(--text);
	opacity: 40%;
	cursor: default;
}

select:disabled:hover,
button:disabled:hover,
.sliderBox input:disabled:hover .disabled:hover {
	filter: brightness(100%);
}

.sliderBox {
	margin-top: -16px;
	display: flex;
	flex-direction: column;
	font-size: 1rem;
}

.sliderBox p {
	text-align: center;
	margin-bottom: 0.3em;
}

.consoleButton {
	transform: scale(1.5);
	padding: 0;
	margin: 0;
	margin-bottom: -0.6em;
	cursor: pointer;
	user-select: none;
	overflow: hidden;
}

@media (max-width: 1000px) {
	nav {
		box-sizing: content-box;
		flex-direction: column;
		height: auto;
		padding: 20px;
	}

	.button,
	.button-disabled,
	select {
		box-sizing: border-box;
		width: 90%;
		height: 50px;
		padding: 12.5px 0;
		margin-bottom: 10px;
		text-align: center;
		text-align-last: center;
	}
	.sliderBox {
		display: flex;
		flex-direction: column;
		font-size: 1rem;
		margin: 10px;
		order: 500;
		width: 90%;
	}

	.sliderBox p {
		text-align: center;
		margin-bottom: 0.3em;
	}

	.timeTaken {
		order: 5000;
		margin-top: 10px;
	}

	.sort {
		margin-top: 0;
		order: -1;
	}

	select {
		order: -1;
	}
}
